*, *:after, *:before{
  box-sizing:border-box;
  margin:0;
  padding:0;
  transition: all 100ms ease-in;
}
.NewYear_new_year__3hTSd{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    padding: 200px 100px 0px 0px;
  }
  .NewYear_new_year__3hTSd>span{
    font-family: 'Cabin Sketch', cursive;
    font-size: 175px;
    font-weight: 700;
    color: #7a8fe8;
  }
  span.NewYear_eight__35-cg{
    position: absolute;
    top: 50%;
    right: 50%;
    margin-right: -200px;
    animation: NewYear_out_2018__2Ooff 5s ease-in-out 5s forwards;
  }
  span.NewYear_nine__3nW2o{
    position: absolute;
    right: 0%;
    margin-right: -200px;
    animation: NewYear_in_2019__1U-Rt 6s ease-in-out forwards;
  }
  span.NewYear_nine__3nW2o:before{
    content: '';
    width: 0px;
    height: 6px;
    display: block;
    border-radius: 3px;
    background: #7a8fe8;
    transform: rotate(0deg);
    transform-origin: left top;
    position: absolute;
    top: 55px;
    left: 10px;
    z-index: -1;
    animation: NewYear_entry_balloon__1YKrD 1s ease-in-out 4s;
  }
  .NewYear_balloon__1TG3-{
    width: 100px;
    height: 100px;
    display: block;
    background: #ffd500;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    margin-top: -165px;
    right: 0%;
    margin-right: -200px;
    animation: NewYear_in_and_out_balloon__2RMzV 10s ease-in-out forwards;
  }
  .NewYear_balloon__1TG3-:before{
    content: '';
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 10px 20px 10px;
    border-color: transparent transparent #f3e309 transparent;
    position: absolute;
    left: 50%;
    margin-left: -10px;
    bottom: -10px;
    z-index: -1;
  }
  .NewYear_balloon__1TG3-:after{
    content: '';
    width: 4px;
    height: 100px;
    display: block;
    background: #fff;
    border-radius: 0px 0px 3px 3px;
    position: absolute;
    left: 50%;
    margin-left: -2px;
    bottom: -110px;
  }
  
  @keyframes NewYear_in_2019__1U-Rt {
    0% {
      right: 0%;
    }
    66.6666% {
      right: 50%;
      margin-right: -300px;
    }
    90% {
      right: 50%;
      margin-right: -300px;
    }
    100% {
      right: 50%;
    }
  }
  @keyframes NewYear_in_and_out_balloon__2RMzV {
    0% {
      right: 0%;
    }
    40% {
      right: 50%;
      margin-right: -300px;
    }
    50% {
      right: 50%;
      margin-right: -200px;
      top: 50%;
    }
    100% {
      top: -100%;
      right: 50%;
    }
  }
  @keyframes NewYear_entry_balloon__1YKrD {
    0% {
      transform: rotate(-30deg);
      width: 40px;
    }
    100% {
      transform: rotate(-150deg);
      width: 70px;
    }
  }
  @keyframes NewYear_out_2018__2Ooff {
    0% {
      top: 50%;
    }
    100% {
      top: -100%;
    }
  }
*, *:after, *:before{
    box-sizing:border-box;
    margin:0;
    padding:0;
    transition: all 100ms ease-in;
  }
.Fireworks_fireworks__3xJKD{
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
  }
  .Fireworks_fireworks__3xJKD>div{
    border: 2px solid #fff;
    position: absolute;
    opacity: 0;
    animation: Fireworks_drop_fireworks__1rqK6 1.5s ease-in-out 8s forwards;
  }
  .Fireworks_fireworks__3xJKD>div.Fireworks_f1__xXprW{
    left: 10%;
    top: 30%;
  }
  .Fireworks_fireworks__3xJKD>div.Fireworks_f2__5g6Iv{
    left: 15%;
    top: 70%;
  }
  .Fireworks_fireworks__3xJKD>div.Fireworks_f3__3u46a{
    right: 10%;
    top: 30%;
  }
  .Fireworks_fireworks__3xJKD>div.Fireworks_f4__1W9ES{
    right: 15%;
    top: 70%;
  }
  .Fireworks_fireworks__3xJKD>div span{
    width: 6px;
    height: 6px;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    animation: Fireworks_burst_fireworks__YywSN 0.5s ease-in-out 9s forwards;
  }
  .Fireworks_fireworks__3xJKD>div span:nth-child(1){
    transform: rotate(0deg);
  }
  .Fireworks_fireworks__3xJKD>div span:nth-child(2){
    transform: rotate(120deg);
  }
  .Fireworks_fireworks__3xJKD>div span:nth-child(3){
    transform: rotate(240deg);
  }
  .Fireworks_fireworks__3xJKD>div span:before{
    content: '';
    width: 2px;
    height: 50px;
    display: block;
    background: #f5cc06;
    position: absolute;
    top: -60px;
    left: 2px;
  }
  .Fireworks_fireworks__3xJKD>div span:after{
    content: '';
    width: 2px;
    height: 50px;
    display: block;
    background: #f5cc06;
    position: absolute;
    bottom: -60px;
    left: 2px;
  }
  .Fireworks_fireworks__3xJKD>div span i:before{
    content: '';
    width: 3px;
    height: 3px;
    display: block;
    border-radius: 50%;
    background: #fff;
    position: absolute;
    top: -15px;
    left: 10px;
  }
  .Fireworks_fireworks__3xJKD>div span i:after{
    content: '';
    width: 3px;
    height: 3px;
    display: block;
    border-radius: 50%;
    background: #fff;
    position: absolute;
    top: -15px;
    right: 10px;
  }
  @keyframes Fireworks_drop_fireworks__1rqK6 {
    0% {
      margin-top: 100%;
      opacity: 0;
      width: 2px;
      height: 30px;
      display: block;
      border-radius: 50%;
    }
    75% {
      margin-top: 0%;
      opacity: 1;
      width: 2px;
      height: 30px;
      display: block;
      border-radius: 50%;
    }
    80%{
      margin-top: 0px;
      margin-left: 0px;
      opacity: 1;
      width: 10px;
      height: 10px;
      display: block;
      border-radius: 50%;
      transform: scale(0.2);
    }
    100%{
      opacity: 1;
      width: 10px;
      height: 10px;
      display: block;
      border-radius: 50%;
      transform: scale(1);
    }
  }
  @keyframes Fireworks_burst_fireworks__YywSN {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
